"use strict";

var modal = require('../../packages/makeup-modal');

var modalEl = document.querySelectorAll('.modal')[0];
var button = document.querySelectorAll('button')[0];
var modalElTwo = document.querySelectorAll('.modal')[1];
var buttonTwo = document.querySelectorAll('button')[1];
var buttonThree = document.querySelectorAll('button')[2];
var modalElThree = document.querySelector('#modal-3');
var unmodalButton = document.querySelectorAll('button')[3];
var hoistCheckbox = document.querySelector('#hoist-checkbox');
button.addEventListener('click', function () {
  modal.modal(modalEl, {
    useHiddenProperty: false,
    hoist: hoistCheckbox.checked
  });
});
buttonTwo.addEventListener('click', function () {
  modal.modal(modalElTwo, {
    useHiddenProperty: false,
    hoist: hoistCheckbox.checked
  });
});
buttonThree.addEventListener('click', function () {
  modal.modal(modalElThree, {
    useHiddenProperty: false,
    hoist: hoistCheckbox.checked
  });
});
unmodalButton.addEventListener('click', function () {
  modal.unmodal();
});